<template>
  <section class="container">
    <div class="login-container">
      <h3 class="title">找回密码</h3>
      <van-field
        left-icon="phone-o"
        v-model="mobile"
        clearable
        placeholder="请输入手机号"
      />
      <van-field
        left-icon="certificate"
        v-model="code"
        placeholder="请输入验证码"
      >
        <van-button
          slot="button"
          size="small"
          type="warning"
          @click="sendCode"
          :disabled="codeDisabled"
          >{{ text }}</van-button
        >
      </van-field>
      <van-field
        left-icon="records"
        v-model="password"
        :type="type"
        clearable
        placeholder="请输入密码"
        :right-icon="rightIcon"
        @click-right-icon="isPassword = !isPassword;"
      />
      <div class="buttons">
        <van-button slot="button" size="small" type="info" @click="go"
          >返回</van-button
        >
        <van-button slot="button" size="small" type="warning" @click="findPw">确定</van-button>
      </div>
      <p>非手机号找回请联系客服QQ:2638474951</p>
    </div>
  </section>
</template>
<script>
import api from '~/service/api';
export default {
  computed: {
    type() {
      return this.isPassword ? 'password' : 'text';
    },
    rightIcon() {
      return this.isPassword ? 'closed-eye' : 'eye';
    }
  },
  data() {
    return {
      // 账户名
      mobile: '',
      // 密码
      password: '',
      // 验证码
      code: '',
      // 切换密码展示
      isPassword: true,
      codeDisabled: false,
      text: '获取验证码'
    };
  },
  methods: {
    async sendCode() {
      if (!this.mobile) {
        this.$toast({
          text: '请输入手机号'
        });
        return;
      }
      this.codeDisabled = true;
      await this.$store.dispatch('SEND_CODE', {
        mobile: this.mobile,
      });
      this.text = 60;
      let total = 60;
      let timer = setInterval(() => {
        total--;
        if (total < 0) {
          clearInterval(timer);
          this.text = '重新获取';
          this.codeDisabled = false;
          return;
        }
        this.text = total;
      }, 1000);
    },
    async findPw() {
      if (!this.mobile) {
        this.$toast({
          text: '请填写手机号'
        })
        return;
      }
      if (!this.code) {
        this.$toast({
          text: '请填写验证码'
        })
        return;
      }
      if (!this.password) {
        this.$toast({
          text: '请填写密码'
        })
        return;
      }
      // 先进行验证码校验
      await this.$store.dispatch('CHECK_CODE', {
        mobile: this.mobile,
        code: this.code
      });
      if (!this.$store.state.account.checkCode) {
        this.$toast({
          text: '验证码错误'
        })
        return;
      }
      await this.$store.dispatch('FIND_PASSWORD', {
        mobile: this.mobile,
        password: this.password
      });
    },
    go() {
      this.$router.go(-1)
    }
  }
};
</script>
